.home{
  
  margin:0;
  padding:0;
  width: 100vw;
  height:100vh;
}
.home_text{
  position:absolute;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
  z-index: 3;

}
.home_canvas{
  
  margin:0;
  padding:0;
  width: 100vw;
  height:100vh;
  background-color:black;
  animation: blackout 3s linear;
}

@keyframes blackout {
  0%{
    background-color: white
  }
  50%{
    background-color: rgba(0, 0, 0, 0.503);
  }
  100%{
    background-color:black;
  }
}




